import {Base64} from 'js-base64'
import axios from 'axios'

axios.defaults.timeout = 6000

var CallAPI = {
    getGoogleSheets : async function (){
        return new Promise((resolve, reject) => {
            axios.get('https://docs.google.com/spreadsheets/d/e/2PACX-1vTewWs8M_IEYtPeVntv3tI8SfkFX8tlR8Vz-F3n5lnB5Ei0HllwWRx1R2xgjA20hB1Wslgq08MdgDME/pub?output=csv').then(result => {
                resolve(result.data);
            }),err => {
                reject(err);
            }
        });
    }
}
export default CallAPI = CallAPI;


