<template>
    <div>
        <div class="wrapper">
            <div class="container">
                <div class="flip-book" id="demoBookExample">
                    <div class="page page-cover page-cover-top" data-density="hard">
                        <div class="page-content">
                            <img width="100%" height="100%" src="https://img.youtube.com/vi/NWaFmsXLAyU/maxresdefault.jpg">
                            <h2><b>神稻櫻火</b><br>生&nbsp;&nbsp;日&nbsp;&nbsp;快&nbsp;&nbsp;樂</h2>
                        </div>
                    </div>
                    <div class="page">
                        <div class="page-content">
                            <h2 class="page-header">神稻櫻火初配信</h2>
                            <div class="page-image" style="background-image: url(https://img.youtube.com/vi/NWaFmsXLAyU/maxresdefault.jpg)"></div>
                            <div class="page-text">
                                今天是神稻櫻火誕生的日子。
                            </div>
                            <div class="page-footer">
                                1
                            </div>
                        </div>
                    </div>
                    <div class="page">
                        <div class="page-content">
                            <h2 class="page-header">虎呱</h2>
                            <div class="page-image">
                                <iframe width="100%" height="100%" src="https://www.youtube.com/embed/RshrT1o2VVk" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                            </div>
                            <div class="page-text">
                                好懷念好懷念的歌呢<br>對火火的500年來說 這應該是相當近的回憶呢<br>不知道有沒有聽過呢<br>
                            </div>
                            <div class="page-footer">
                                2
                            </div>
                        </div>
                    </div>
                    <div v-for="(item, index) in bless">
                        <div class="page">
                            <div class="page-content">
                                <h2 class="page-header">{{item[1]}}</h2>
                                <div class="page-image" >
                                    <iframe width="100%" v-if="item[4]" height="100%" :src="item[4]" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                                    <img width="100%" v-if="item[3]" :src="item[3]" loading="lazy">
                                    <img width="100%" v-if="!item[3] && !item[4]" :src="i1" loading="lazy">
                                </div>
                                <div class="page-text" v-html="item[2]">
                                </div>
                                <div class="page-footer">
                                    {{index + 3}}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="page page-cover page-cover-bottom" data-density="hard">
                        <div class="page-content">
                            <h2>THE END</h2>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
axios.defaults.timeout = 6000
import {Base64} from 'js-base64'
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import i1 from "../assets/img/default.jpg"
export default {
    name: 'reserve',
    components: { DatePicker },
    data () {
        return {
            i1: i1,
            bless : []
        }
    },
    methods: {
    },
    async mounted() {
        let rt =  await this.$CallAPI.getGoogleSheets();
        let ex = rt.split(',\r\n');
        for(let inx in ex) {
            if(inx > 0) {
                let ex2 = [];
                ex2 = ex[inx].split(',');
                if(ex2[2]) {
                    ex2[2] = ex2[2].replace(/"/g, '').replace(/(?:\\[rn]|[\r\n]+)+/g, '<br>');
                    console.log(ex2[2]);
                }
                if(ex2[3]) {
                    ex2[3] = 'https://drive.google.com/uc?export=download&id=' + ex2[3].split('https://drive.google.com/open?id=')[1];
                }
                if(ex2[4]) {
                    ex2[4] = 'https://www.youtube.com/embed/' + ex2[4].split('https://www.youtube.com/watch?v=')[1];
                }
                this.bless.push(ex2);
            }
        }
        console.log(ex);
        const pageFlip = await new St.PageFlip(
            document.getElementById("demoBookExample"),
            {
                width: 550, // base page width
                height: 733, // base page height

                size: "stretch",
                // set threshold values:
                minWidth: 315,
                maxWidth: 1000,
                minHeight: 420,
                maxHeight: 1350,

                maxShadowOpacity: 0.5, // Half shadow intensity
                showCover: true,
                mobileScrollSupport: false // disable content scrolling on mobile devices
            }
        );

        // load pages
        pageFlip.loadFromHTML(document.querySelectorAll(".page"));

        document.querySelector(".page-total").innerText = pageFlip.getPageCount();
        document.querySelector(
            ".page-orientation"
        ).innerText = pageFlip.getOrientation();

        document.querySelector(".btn-prev").addEventListener("click", () => {
            pageFlip.flipPrev(); // Turn to the previous page (with animation)
        });

        document.querySelector(".btn-next").addEventListener("click", () => {
            pageFlip.flipNext(); // Turn to the next page (with animation)
        });

        // triggered by page turning
        pageFlip.on("flip", (e) => {
            document.querySelector(".page-current").innerText = e.data + 1;
        });

        // triggered when the state of the book changes
        pageFlip.on("changeState", (e) => {
            document.querySelector(".page-state").innerText = e.data;
        });

        // triggered when page orientation changes
        pageFlip.on("changeOrientation", (e) => {
            document.querySelector(".page-orientation").innerText = e.data;
        });
    }
}
</script>



